
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  padding: 2rem;
}
#app .demo .options{
  border: 1px solid #dedede;
  margin-top: 1rem;
  padding: 2rem;
}
#app .demo .options select, input{
  font-size: 1rem;
  width: 10rem;
  height: 2rem;
  border-radius: 0;
}
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.vue-lite-spinner{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
}
.vue-lite-spinner .loader {
    border: 10px solid #f3f3f3; /* Light grey */
    border-top: 10px solid #3498db; /* Blue */
    border-radius: 50%;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
    -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.55);
            box-shadow: 0 0 1px rgba(0, 0, 0, 0.55);
}
.vue-lite-spinner .text{
    -moz-user-select: none; 
    -o-user-select:none; 
    -webkit-user-select:none; 
    -ms-user-select:none; 
    user-select:none;
    color: #dedede;
}
.vue-lite-spinner .position-center{
    min-width: 5rem;
    min-height: 3rem;
    padding: .4rem;
    /** background: #000000;*/
    margin: 25% auto;
}
.vue-lite-spinner .position-top{
    position: absolute;
    top: 0;
    min-width: 5rem;
    height: 3rem;
    padding: .4rem;
    /** background: #000000;*/
}
.vue-lite-spinner .position-bottom{
    position: absolute;
    bottom: 5rem;
    min-width: 5rem;
    height: 3rem;
    padding: .4rem;
    /** background: #000000;*/
}
.vue-lite-spinner .position-center .loader{
    margin: 0 auto;
}
.vue-lite-spinner .position-top .loader{
    float: left;
}
.vue-lite-spinner .position-bottom .loader{
    float: left;
}
.vue-lite-spinner .position-center .loader{
    margin: 0 auto;
}
.vue-lite-spinner .position-center .text{
    padding-top: 1rem;
    padding-left: 0!important;
    font-size: 1.2rem;
    text-align: center;
}
.vue-lite-spinner .position-top .text{
    float: left;
    font-size: 1.2rem;
}
.vue-lite-spinner .position-bottom .text{
    float: left;
    font-size: 1.2rem;
}